.apply-online {
  position: relative;
}
.apply-online .add {
  width: 3.75rem;
  padding: 0.16rem;
  border-bottom: 1px solid #EEEEEE;
  box-sizing: border-box;
}
.apply-online .add a {
  width: 3.4rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  border: 0.01rem solid #dba131;
  line-height: 0.4rem;
  color: #dba131;
  font-size: 0.16rem;
  text-align: center;
}
.apply-online .apply-item {
  width: 3.75rem;
  padding: 0.16rem;
  box-sizing: border-box;
  border-bottom: 1px solid #EEEEEE;
}
.apply-online .apply-item .apply-item-c-l {
  width: 2.3rem;
}
.apply-online .apply-item .apply-item-c-l h3 {
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.16rem;
  margin-bottom: 0.05rem;
}
.apply-online .apply-item .apply-item-c-l h3 span {
  margin-left: 0.3rem;
}
.apply-online .apply-item .apply-item-c-l h3 .am-tag-disabled {
  margin-left: 0.2rem;
}
.apply-online .apply-item .apply-item-c-l p {
  line-height: 0.22rem;
  color: #999999;
  font-size: 0.14rem;
}
.apply-online .apply-item .apply-item-c-r {
  padding: 0.3rem 0;
}
.apply-online .apply-item .apply-item-c-r a {
  height: 0.3rem;
  line-height: 0.3rem;
  color: #999999;
  font-size: 0.14rem;
  width: 0.5rem;
  text-align: right;
}
.apply-online .apply-item .apply-item-c-r .del-per {
  height: 0.3rem;
  line-height: 0.3rem;
  color: #999999;
  font-size: 0.14rem;
  width: 0.5rem;
  text-align: right;
}
.apply-online .am-checkbox-inner {
  border-radius: 0;
}
.apply-online .am-list-item .am-list-line {
  padding-right: 0;
}
.apply-online .apply-sign {
  position: fixed !important;
  bottom: 0;
  height: 0.7rem;
  background: #fff;
  z-index: 15;
  width: 100%;
}
.apply-online .apply-sign .apply-sign-btn {
  margin-left: 0.15rem;
  width: 3.43rem;
  height: 0.4rem;
  line-height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  font-size: 0.16rem;
}
